.nav{
    position: fixed;
    top: 0;
    padding: 20px;
    width: 100%;
    height: 30px;
    z-index: 1;
    transition-timing-function: ease-in;
    transition: all 1s;
}

.nav__contents{
    display: flex;
    justify-content: space-between;
}
.nav__black{
    background-color: black;
}
.nav__logo{
    position: fixed;
    left: 0;
    top: 20px;
    width: 80px;
    object-fit: contain;
    padding-left: 30px;
    cursor: pointer;
}
.nav__avatar{
    cursor: pointer;
    position: fixed;
    right: 20px;
    width: 40px;
}